@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600&display=swap);
html, body, #root {
  height: 100%;
  width: 100%; }

* {
  font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

::-webkit-scrollbar {
  width: 5px; }

::-webkit-scrollbar-thumb {
  background: #18C657; }

::-webkit-scrollbar-track {
  background: #2C2C2C; }

.button {
  color: #fff;
  background-color: #18C657;
  border-width: 0;
  outline: none;
  border-radius: 3px;
  height: auto;
  width: auto;
  padding: 7px;
  font-weight: 500; }

.loading {
  -webkit-animation: rotation infinite 5s linear;
          animation: rotation infinite 5s linear; }

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.login {
  display: flex;
  height: 100%;
  width: 100%;
  padding: 50px 0 0 60px;
  background-color: #2c2c2c; }
  .login::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 100%;
    background-color: #18C657; }
  .login__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-right: 100px; }
    .login__content__description {
      width: 255px; }
      .login__content__description span {
        display: block; }
      .login__content__description__title {
        font-size: 25px;
        color: #efefef;
        margin-bottom: 20px;
        font-weight: 500; }
      .login__content__description__subtitle {
        font-size: 14px;
        color: #efefef; }
  .login .button {
    margin-top: 20px; }

.login-loading {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  background-color: #2c2c2c; }

.profile {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .profile span {
    color: #efefef; }
  .profile__avatar {
    width: 130px;
    height: 130px;
    border-radius: 50%; }
  .profile__informations {
    display: flex;
    flex-direction: column; }
    .profile__informations__name {
      font-weight: 500;
      font-size: 20px;
      margin-top: 20px; }
    .profile__informations__account {
      margin-top: 5px;
      font-size: 11px; }
  .profile--minimal {
    flex-direction: row; }
    .profile--minimal__avatar {
      height: 60px;
      width: 60px; }
    .profile--minimal__informations {
      margin-left: 10px; }
      .profile--minimal__informations__name {
        margin-top: 0; }

.playlistItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 18px;
  margin-bottom: 18px;
  max-width: 150px; }
  .playlistItem__image {
    height: 150px;
    width: 150px; }
  .playlistItem__name, .playlistItem__amount {
    color: #efefef; }
  .playlistItem__name {
    font-size: 15px;
    font-weight: 500;
    margin-top: 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 150px; }
  .playlistItem__amount {
    font-size: 10px;
    font-weight: 400;
    margin-top: 4px; }

.playlists {
  display: flex;
  height: 100%;
  width: 100%;
  background: -webkit-gradient(linear, left bottom, left top, from(#0f0c0c), to(#313131));
  background: -webkit-linear-gradient(bottom, #0f0c0c, #313131);
  background: linear-gradient(0deg, #0f0c0c, #313131);
  flex-direction: column; }
  .playlists__logo {
    margin: 50px 0 0 60px; }
  .playlists__content {
    display: flex;
    align-self: center;
    flex-direction: column;
    height: 100%;
    width: 100%; }
    .playlists__content__items {
      display: flex;
      margin-top: 30px;
      width: 100%;
      justify-content: center;
      flex-flow: row wrap;
      overflow-x: hidden;
      overflow-y: auto;
      white-space: nowrap;
      max-height: 60%; }

.searchBar {
  position: relative;
  display: flex;
  width: 100%;
  height: 60px;
  min-height: 60px;
  background-color: #1a1a1a;
  min-width: 622px; }
  .searchBar::before {
    content: " ";
    position: absolute;
    left: 25px;
    top: 14px;
    width: 2px;
    height: 36px;
    -webkit-transform: scale(0);
            transform: scale(0);
    z-index: 1;
    background: #18C657;
    -webkit-transition: all 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44);
    transition: all 0.5s cubic-bezier(0.87, -0.41, 0.19, 1.44); }
  .searchBar__input {
    display: block;
    margin: 0;
    color: #efefef;
    background: transparent;
    font-family: inherit;
    border: none;
    padding: 15px;
    font-size: 30px;
    font-weight: 500;
    caret-color: #1ab26b;
    width: 100%;
    height: 100%; }
    .searchBar__input::-webkit-input-placeholder {
      color: #5a5a5a; }
    .searchBar__input::-moz-placeholder {
      color: #5a5a5a; }
    .searchBar__input:-ms-input-placeholder {
      color: #5a5a5a; }
    .searchBar__input::-ms-input-placeholder {
      color: #5a5a5a; }
    .searchBar__input::placeholder {
      color: #5a5a5a; }
    .searchBar__input:focus {
      outline: none; }

.playlistItems {
  display: flex;
  flex-direction: column;
  margin-top: 30px; }
  .playlistItems__nameAndTime {
    display: flex;
    align-items: center;
    justify-content: space-between; }
    .playlistItems__nameAndTime__name {
      color: #efefef;
      font-weight: bold; }
      .playlistItems__nameAndTime__name::before {
        display: inline-block;
        vertical-align: sub;
        width: 20px;
        content: "\2022";
        color: #18C657;
        font-size: 30px;
        font-weight: bold; }
    .playlistItems__nameAndTime__time {
      color: #B3B3B3;
      font-weight: 500; }
  .playlistItems__artist {
    color: #B3B3B3;
    font-size: 10px;
    font-weight: 500;
    margin-left: 20px;
    margin-bottom: 15px; }

.playlist {
  display: flex;
  height: 100%;
  width: 100%;
  background: -webkit-gradient(linear, left bottom, left top, from(#0f0c0c), to(#313131));
  background: -webkit-linear-gradient(bottom, #0f0c0c, #313131);
  background: linear-gradient(0deg, #0f0c0c, #313131);
  flex-direction: row; }
  .playlist__info {
    min-width: 280px;
    width: 280px;
    height: 100%;
    background-color: #0c0909;
    display: flex;
    flex-direction: column; }
    .playlist__info__logo {
      margin-top: 50px;
      margin-left: 60px; }
    .playlist__info__playlist {
      margin-top: 40px;
      margin-right: 0;
      align-self: center; }
  .playlist__content {
    display: flex;
    margin: 60px 80px 0;
    flex-direction: column; }
    .playlist__content__backNavigation {
      margin-bottom: 40px;
      min-height: 35px; }
    .playlist__content__tracks {
      display: flex;
      padding-right: 15px;
      overflow-x: hidden;
      overflow-y: auto;
      white-space: nowrap;
      max-height: 100%; }
  .playlist__profile {
    align-self: flex-start;
    margin-top: 50px; }

